const submitButton = document.getElementById('submit');
const previousButton = document.getElementById("previous");
const nextButton = document.getElementById("next");
const blockButton = document.querySelector('.quiz__block-btn')
const formQuiz = document.getElementById('form');
const endBlock = document.getElementById('end-block');
const slides = document.querySelectorAll(".quiz__slide");
let currentSlide = 0;
function showSlide(n) {
  slides[currentSlide].classList.remove('active-slide');
  slides[n].classList.add('active-slide');
  currentSlide = n;
  if (currentSlide === 0) {
    previousButton.classList.add('hide');
  }
  else {
    previousButton.classList.remove('hide');
  }
  if (currentSlide === slides.length - 1) {
    blockButton.style.display = 'none';
    submitButton.style.display = 'inline-block';
  }
  else {
    nextButton.style.display = 'inline-block';
    submitButton.style.display = 'none';
  }
}
function showNextSlide() {
  if (nextButton?.classList.contains("button--active")) {
    showSlide(currentSlide + 1);
    if (!valid(currentSlide)) {
      nextButton.classList.remove('button--active');
    }
  }
}
function showPreviousSlide() {
  showSlide(currentSlide - 1);
  nextButton.classList.add('button--active');
};
function valid(currentSlide) {
  let isValid = false;
  let elements = slides[currentSlide].querySelectorAll('input');

  elements.forEach(el => {
    switch (el.type) {
      case 'text':
        (el.value) ? isValid = true : el.classList.add('error');
      case 'checkbox':
        (el.checked) ? isValid = true : el.classList.add('error');
      case 'radio':
        (el.checked) ? isValid = true : el.classList.add('error');
    }
  });
  return isValid;
}
showSlide(currentSlide);
formQuiz?.addEventListener('change', (e) => {
  if (e.target.tagName == 'INPUT' || e.target.tagName == 'SELECT') {
    nextButton.classList.add('button--active');
  }
});
previousButton.addEventListener("click", showPreviousSlide);
nextButton.addEventListener("click", showNextSlide);
formQuiz.addEventListener('submit', handleFormSubmit);
